import React from 'react';
import styles from './Button.module.css';

type ButtonProps = {
    children?: React.ReactNode;
    onClick?: () => void;
    color?: 'primary' | 'secondary';
    variant?: 'contained' | 'outlined';
    size?: 'small' | 'medium' | 'large';
    disabled?: boolean;
};

const Button: React.FC<ButtonProps> = ({ children, onClick, color = 'primary', variant = 'contained', size = 'medium', disabled = false }) => {
    return (
        <button
            disabled={disabled}
            className={`${styles.button} ${styles[size]} ${styles[color]} ${styles[variant]}`}
            onClick={onClick}
        >
            {children}
        </button>
    );
};

export default Button;